import { useState, useEffect } from "react"

const useModeToggle = (data) => {
  /*
    Returns current theme mode ("light" or "dark"), theme mode toggle function
    and a boolean for if the theme mode toggle is to be allowed.
  */
  const allowChange = data.allowChange
  const [mode, setMode] = useState()

  useEffect(() => {
    const defaultMode =
      data.allowChange && window.localStorage.getItem("hello-friend-ng-mode")
        ? window.localStorage.getItem("hello-friend-ng-mode")
        : data.default

    setMode(defaultMode)
  })

  const toggleMode = () => {
    const newMode = mode === "light" ? "dark" : "light"
    window.localStorage.setItem("hello-friend-ng-mode", newMode)
    setMode(newMode)
  }

  return [mode, toggleMode, allowChange]
}

export default useModeToggle
